.play {
  background-color: rgba($primary, 1);
  background-image: var(--bs-gradient);
  position: absolute;
  height: 100%;
  width: 100%;
  overflow: hidden;

  &-main {
    position: relative;
    width: 100%;
    height: 100%;
  }
}

.draggie {
  position: absolute;
  margin-top: 0.5rem;
  height: calc(100% - 1rem);
  width: auto;
  right: 0;

  &-btn {
    display: block;
    width: 3.5rem;
    height: 3.5rem;
    top: 1rem;
    // margin-top: -50%;
    border-radius: 100px;
    background-color: $light;
    background-image: var(--bs-gradient);
    z-index: 1;

    &::before {
      content: '';
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      background: url(../images/f1-icon.svg) center no-repeat;
      background-size: 65% auto;
    }

    &.is-pointer-down {
      opacity: .75;
    }

    &.on {
      opacity: 0;
    }
  }
}

.game-content {
  max-width: 500px;
  width: calc(100% - 2rem);
  height: calc(100% - 2rem);
  top: 1rem;
  border-radius: 1.5rem 0 0 1.5rem;
  transform: translateX( calc(100% + 1.5rem));
  // @include media-breakpoint-up(sm) {
  //   left: auto;
  //   right: 1rem;
  // }
}

.btn-close-offcanvas {
  position: absolute;
  border-radius: 100px;
  background-color: $primary;
  color: $light;
  width: 3rem;
  height: 3rem;
  top: 50%;
  transform: translate(-50%, -50%);
  opacity: 1;
  transition: all .3s;
  z-index: 1;
  &:hover {
    color: rgba($light, .5);
    background-color: darken($primary, 10);
  }
}

.offcanvas-header {
  flex-wrap: wrap;
}

.logo-gamepage {
  display: block;
  text-align: center;

  img {
    max-width: 3rem;
    width: 100%;
    height: auto;
  }
}

.nav-icon {
  // margin-top: .5rem;

  >.nav-link {
    background-color: $primary;
    line-height: 2rem;
    color: $light;
    border-radius: 0.75rem;
    margin-left: 0.25rem;

    &:nth-child(1) {
      margin-left: 0;
    }

    &:hover {
      filter: brightness(1.1);
    }
  }
}

.user {
  display: flex;
  width: 100%;
  align-items: center;
  flex-wrap: wrap;
  background-color: $light;
  border-radius: 1.5rem;
  padding: 1rem;
  margin-top: 1rem;
  box-shadow: 0 0 10px 0px $gray-300;

  &-img {
    width: 6.5rem;
    height: 6.5rem;
    background:
      url(../images/user.svg) center/50% auto no-repeat,
      linear-gradient(220deg, rgba($primary, .5) 0%, #FFC8C8 100%);
    border: .25rem solid $gray-300;
    border-radius: 500px;
    overflow: hidden;

    img {
      max-width: 100%;
      height: auto;
    }

    @include media-breakpoint-up(sm) {
      width: 8rem;
      height: 8rem;
    }
  }

  &-data {
    width: 75%;
    flex: 1;

    >* {
      margin-bottom: .25rem;
    }

    .nav-fill {
      >a {
        flex: 1;
      }
    }

    h5 {
      font-weight: bold;
      display: flex;
      align-items: start;

      &::before {
        content: 'ID';
        display: inline-block;
        background-color: $primary;
        border-radius: .25rem;
        color: $light;
        font-size: .5rem;
        padding: .2rem .5rem;
        margin-right: .25rem;
      }
    }

    p {
      display: flex;
      align-items: center;

      >a {
        margin-left: auto;
        text-decoration: none;

        @include media-breakpoint-up(sm) {
          font-size: .9em;

          &::after {
            content: '隱私權';
            margin-left: .25rem;
          }
        }
      }
    }
  }

  &-pay {
    padding: 1rem;
    margin-top: .5rem;
    width: 100%;
    border-radius: 1rem;
    background-image: linear-gradient(220deg, rgba($primary, .1) 0%, rgba($primary, .05) 100%);
    display: flex;
    align-items: center;

    >p {
      margin-bottom: 0;

      >b {
        font-size: 1.25rem;
      }
    }
  }
}

/*a*/
.game-list {
  display: flex;
  align-items: start;
  margin-bottom: .75rem;
  color: $body-color;
  text-decoration: none;
  background-color: $light;
  padding: 1rem;
  border-radius: 1rem;
  transition: background-color .3s ease-in-out;

  &:hover {
    background-color: rgba($primary, .1);
  }

  @include media-breakpoint-up(sm) {
    align-items: center;
  }

  img {
    background-color: rgba($primary, 1);
    margin-right: 0.5rem;
    max-width: 50px;
    border-radius: 25%;

    @include media-breakpoint-up(sm) {
      max-width: 64px;
    }
  }

  &-intro {
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;

    @include media-breakpoint-up(sm) {
      white-space: inherit;
    }

    p {
      margin-bottom: .25rem;
      font-size: 1.25rem;
    }

    small {
      color: $secondary;
    }

    .badge {
      font-weight: normal;
      font-size: 0.75rem;
      line-height: initial;
      &:nth-child(n+2) {
        display: none;
      }

      //n為0 1
      &:nth-child(3n+1) {
        background-color: rgba($primary, 1);
      }

      // 2
      &:nth-child(3n-1) {
        background-color: rgba($dark, 0.75);
      }

      // 3
      &:nth-child(3n) {
        background-color: rgba($primary, 0.75);
      }
      @include media-breakpoint-up(sm) {
        &:nth-child(n+2) {
          display: inline-block;
        }
      }
    }

  }
}

/*dl*/
.game-gift {
  background-color: $light;
  border-radius: 1rem;
  padding: 1rem;
  margin-bottom: 0.75rem;
  transition: background-color .3s ease-in-out;
  
  dd {
    margin-bottom: 0;
    display: flex;
    justify-content: space-between;
    align-items: last baseline;
  }
  
  p {
    // line-height: 1.75rem;
    margin-bottom: 0;
    flex: 1;
    width: auto;
    max-width: 75%;
    margin-right: .5rem;
  }
  
  small {
    display: block;
    margin-top: 0.25rem;
  }
  .badge {
    font-weight: normal;
  }
}

.gift-number {
  p {
    background-color: $gray-300;
    border-radius: .5rem;
    padding: .5rem 1rem;
    margin-top: .5rem;
  }

  button {
    position: relative;
    display: inline-block;

    &::before {
      content: "Copied";
      color: $light;
      position: absolute;
      top: calc(-100% - 8px);
      left: 50%;
      transform: translateX(-50%);
      background: $primary;
      padding: .25rem .5rem;
      border-radius: 20px;
      width: 5rem;
      display: none;
    }

    &::after {
      content: "";
      position: absolute;
      top: -12px;
      margin: auto;
      left: 0;
      right: 0;
      width: 6px;
      height: 6px;
      background: $primary;
      transform: rotate(45deg);
      display: none;
    }

    &.active {
      &::before,&::after {
        display: block;
      }
    }
  }
}

.offcanvas-footer {
  a.active {
    &:nth-child(1){
      &::after {
        content: '禮包';
      }
    }
    &:nth-child(2){
      &::after {
        content: '遊戲';
      }
    }
    &:nth-child(3){
      &::after {
        content: '新開服';
      }
    }
  }

}