$line: #00c300;
$fb: #0b86ef;
$google: #fff;

.login-modal {
  img {
    display: block;
    margin-left: auto;
    margin-right: auto;
    width: auto;
    max-width: 100%;
  }

  p {
    margin-bottom: 0.5rem;
    color: rgb(126, 126, 126);
  }

  nav {
    margin-bottom: .5rem;
    margin-top: 0.5rem;
    display: flex;
    flex-wrap: wrap;
    flex-direction: column;
    @include media-breakpoint-up(sm) {
      flex-direction: row;
    }
  }

  .btn {
    color: #fff;
    flex: 1;
    font-size: 1.2rem;
    line-height: 2rem;
    padding: 1rem 1rem 1rem 3rem;
    margin-top: .5rem;
    text-align: center;
    transition: all .25s;
    width: 100%;
    @include media-breakpoint-up(sm) {
      margin-top: 0;
      margin-left: 0.5rem;
      // width: calc(33% - 1rem) ;

      &:first-child {
        margin-left: 0;
      }
      &:last-child {
        min-width: 100%;
        margin-left: 0;
        margin-top: 0.5rem;
      }
    }
    &:hover {
      filter: brightness(1.1);
    }

    &::before {
      position: absolute;
      transform: translateX(-120%);
      width: 3rem;
      height: 2rem;
      content: "";
    }

    &-line {
      background-color: $line;

      // box-shadow:0 6px 0 darken($line, 5);
      &::before {
        background: url(../images/loginModal/line.svg) center no-repeat;
        background-size: auto 90%;
        border-right: 1px solid darken($line, 5);
      }
    }

    &-fb {
      background-color: $fb;
      &::before {
        background: url(../images/loginModal/fb.svg) center no-repeat;
        background-size: auto 80%;
        border-right: 1px solid darken($fb, 10);
      }
    }

    &-google {
      background-color: $google;
      color: #868686;
      border: 1px solid darken($google, 10);

      &::before {
        background: url(../images/loginModal/google.png) center no-repeat;
        background-size: auto 80%;
        border-right: 1px solid darken($google, 10);
      }

      &:hover {
        filter: brightness(0.9);
      }
    }

    &-meta {
      background-color: $google;
      color: #3b3b3b;
      border: 1px solid darken($google, 10);

      &::before {
        background: url(../images/loginModal/MetaMask.png) center no-repeat;
        background-size: auto 80%;
        border-right: 1px solid darken($google, 10);
      }

      &:hover {
        filter: brightness(0.9);
      }
    }
  }

}